/* //palette csss
 */
.left {
  position: fixed;
  height: 90vh;
  top: 10vh;
  width: 18%;
  left: 0;
  overflow-y: scroll;
}

.right {
  position: absolute;
  right: 0;
  top: 10vh;
  display: block;
  width: 82%;
  margin-left: 18%;
  overflow: hidden;
}
@media (max-width: 1200px) {
  .left {
    width: 100%;
    display: flex;
    position: fixed;
    top: auto;
    left: 0;
    bottom: 0px;
    height: auto;
    overflow-x: scroll;
    overflow-y: auto;
    z-index: 999;
    background: rgb(248, 242, 242);
  }
  .left::-webkit-scrollbar {
    width: 10px;
    height: 10px;
  }
  .left::-webkit-scrollbar-thumb {
    background: rgba(210, 210, 210, 0.8);
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5);
  }
  .left::-webkit-scrollbar-track {
    background-color: #fff;
    box-shadow: inset 0 0 5px grey;
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  }
  .right {
    width: 100%;
  }
  .palette_div {
    margin: 10px 16px !important;
    padding: 0 !important;
    justify-content: space-around;
  }
  .palette_div_item {
    height: 20px;
    width: 24px !important;
  }
}

